export function savestore (id,key,val) {      //在一个本地存储中，存储以不同商家id为对象的一个大对象，不同的商家id对象中再存储不同商家的对象信息
  let store = window.localStorage.store;
  if(!store){           //判断没有存储，没有的情况下
    store = {};          //建立大存储对象
    store[id] = {};      //建立大存储对象下的不同id的子对象
  }else {
    store = JSON.parse(store);
    if(!store[id]){         //在有存储情况下，判断有没有商家id的子对象，没有的话就建立
      store[id] = {};
    }
  }
  store[id][key] = val;
  window.localStorage.store = JSON.stringify(store);

};
export function readstore (id,key,def) {
  let store = window.localStorage.store;       //def默认值
  if(!store){
    return def;
  }
  store = JSON.parse(store);
  if(!store[id]){
    return def;
  }
  return store[id][key]?store[id][key]:def;
};



