<template>
  <div class="header">
    <div class="content">
      <div class="avatar">
        <img :src="seller.avatar" width="64" height="64"/>
      </div>
      <div class="con">
        <div class="title">
          <span class="brand"></span><span class="name">{{seller.name}}</span>
        </div>
        <div class="description">{{seller.description}}/{{seller.deliveryTime}}分钟到达</div>
        <div v-if="seller.supports" class="support">
          <span class="icon" :class="classmap[seller.supports[0].type]"></span><span class="text">{{seller.supports[0].description}}</span>
        </div>
        <div class="support-count" v-if="seller.supports" @click="detialshow=true">
          <span class="count">{{seller.supports.length}}个</span><i class="icon-keyboard_arrow_right"></i>
        </div>
      </div>
    </div>
    <div class="notice" @click="detialshow=true">
      <span class="bulletin-img"></span><span class="bulletin-text">{{seller.bulletin}}</span>
      <i class="icon-keyboard_arrow_right"></i>
    </div>
    <div class="background">
      <img :src="seller.avatar" width="100%" height="100%"/>
    </div>
    <transition name="fade">
      <div class="detial" v-show="detialshow">
        <div class="detial-main clearfix">
          <div class="detial-text">
            <h1 class="name">{{seller.name}}</h1>
            <div class="star-wrapper">
              <star :size="48" :score="seller.score"></star>
              <!--星星评分组件-->
            </div>
            <div class="title">
              <div class="line"></div>
              <div class="text">优惠信息</div>
              <div class="line"></div>
            </div>
            <ul v-if="seller.supports" class="support">
              <li v-for="val in seller.supports" class="item">
                <span class="icon" :class="classmap[val.type]"></span><span class="text">{{val.description}}</span>
              </li>
            </ul>
            <div class="title">
              <div class="line"></div>
              <div class="text">商家公告</div>
              <div class="line"></div>
            </div>
            <div class="bulletin">
              <p class="text">{{seller.bulletin}}</p>
            </div>
          </div>
        </div>
        <div class="close" @click="detialshow=false">
          <i class="icon-close"></i>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
import star from '../star/star.vue'
export default {
  data () {
    return {
      classmap:['decrease','discount','guarantee','invoice','special'],
      detialshow:false
    }
  },
  props: ['seller'],
  components:{
      star
  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .header{color: white;background: rgba(7,17,27,0.5);position: relative;overflow: hidden;}

  .header .content{padding: 24px 12px 18px 24px;font-size: 0;position: relative;}
  .header .content .avatar{display: inline-block;margin-right: 16px;vertical-align: top;}
  .header .content .con{display: inline-block;font-size: 14px;}
  .header .content .con .title{margin: 2px 0 8px 0;}
  .header .content .con .brand{background: url("./brand@2x.png") no-repeat;display: inline-block;height: 18px;width: 30px;background-size: cover;vertical-align: top;margin-right: 6px;}
  .header .content .con .name{font-size: 16px;line-height: 18px;font-weight: bold;}
  .header .content .con .description{font-size: 12px;font-weight: 200;line-height: 12px;margin-bottom: 10px;}
  .header .content .con .support .decrease{background: url("./decrease_1@2x.png") no-repeat;}
  .header .content .con .support .discount{background: url("./discount_1@2x.png") no-repeat;}
  .header .content .con .support .guarantee{background: url("./guarantee_1@2x.png") no-repeat;}
  .header .content .con .support .invoice{background: url("./invoice_1@2x.png") no-repeat;}
  .header .content .con .support .special{background: url("./special_1@2x.png") no-repeat;}
  .header .content .con .support .icon{display: inline-block;height: 12px;width: 12px;background-size: cover;vertical-align: middle;margin-right: 4px;}
  .header .content .con .support .text{font-size: 10px;font-weight: 200;line-height: 12px;}
  .header .content .con .support-count{position: absolute;right: 12px;bottom: 18px;background: rgba(0,0,0,0.2);height:24px;line-height: 24px;text-align: center;padding: 0 8px;border-radius: 14px;font-size: 10px;font-weight: 200;}
  .header .content .con .support-count .count{margin-right: 2px;}
  .header .content .con .support-count .icon-keyboard_arrow_right{vertical-align: top;line-height: 24px;}

  .header .notice{height: 28px;line-height: 28px;font-size: 10px;color: white;font-weight: 200;background: rgba(7,17,27,0.2);overflow: hidden;white-space: nowrap;text-overflow: ellipsis;padding: 0 16px 0 12px;position: relative;}
  .header .notice .bulletin-img{background: url("./bulletin@2x.png") no-repeat;display: inline-block;width: 22px;height: 12px;background-size: cover;vertical-align: top;margin-top: 8px;margin-right: 4px;}
  .header .notice .icon-keyboard_arrow_right{position: absolute;right:12px;top:8px;}

  .header .background{position: absolute;left: 0;top:0;width: 100%;height: 100%;z-index: -1;filter: blur(5px);}

  .header .detial{position: fixed;left: 0;top:0;background: rgba(7,17,27,0.8);z-index: 100;height: 100%;width: 100%;overflow: auto;}
  .header .detial .detial-main{min-height: 100%;width: 100%;}
  .header .detial .detial-main .detial-text{padding-top: 64px;padding-bottom: 64px;}
  .header .detial .close{height: 32px;width: 32px;font-size: 32px;color: rgba(255,255,255,0.5);margin: -50px auto 0 auto;}
  .header .detial .detial-main .detial-text h1{font-size: 16px;line-height: 16px;text-align: center;font-weight: 700;color: white;}
  .header .detial .detial-main .detial-text .star-wrapper{margin-top: 16px;text-align: center;}
  .header .detial .detial-main .detial-text .title{display: flex;margin: 28px auto 24px auto;width: 80%;}
  .header .detial .detial-main .detial-text .title .line{flex-grow: 1;border-bottom: 1px solid rgba(255,255,255,0.2);position: relative;top:-6px;}
  .header .detial .detial-main .detial-text .title .text{font-size: 14px;font-weight: 700;color: white;line-height: 14px;padding: 0 12px;}
  .header .detial .detial-main .detial-text .support{width: 80%;margin: 0 auto;}
  .header .detial .detial-main .detial-text .support .item{padding: 0 12px;margin-bottom: 12px;}
  .header .detial .detial-main .detial-text .support .item:last-child{margin-bottom: 0;}
  .header .detial .detial-main .detial-text .support .decrease{background: url("./decrease_2@2x.png") no-repeat;}
  .header .detial .detial-main .detial-text .support .discount{background: url("./discount_2@2x.png") no-repeat;}
  .header .detial .detial-main .detial-text .support .guarantee{background: url("./guarantee_2@2x.png") no-repeat;}
  .header .detial .detial-main .detial-text .support .invoice{background: url("./invoice_2@2x.png") no-repeat;}
  .header .detial .detial-main .detial-text .support .special{background: url("./special_2@2x.png") no-repeat;}
  .header .detial .detial-main .detial-text .support .icon{display: inline-block;height: 16px;width: 16px;background-size: cover;vertical-align: middle;margin-right: 6px;}
  .header .detial .detial-main .detial-text .support .text{font-size: 12px;line-height: 16px;font-weight: 200;color: white;}
  .header .detial .detial-main .detial-text .bulletin{width: 80%;margin: 0 auto;}
  .header .detial .detial-main .detial-text .bulletin .text{padding: 0 12px;font-size: 12px;font-weight: 200;line-height: 24px;color: white;}

  .fade-enter,.fade-leave-to{opacity: 0;}
  .fade-enter-active,.fade-leave-active{transition: all 0.5s;}

</style>



