<template>
  <div class="star" :class="starType">
    <span v-for="itemClass in starArr" class="star-item" :class="itemClass"></span>
  </div>
</template>

<script>

const lengh = 5;
const cls_on = 'on';
const cls_half = 'half';
const cls_off = 'off';

export default {

  props:['size','score'],
  computed:{
    starType:function () {
        return 'star-'+ this.size;
    },
    starArr:function () {        //计算数组中循环的每个元素，确定改元素的itemClass类（开，半，关），即['on','half','off']的数组
        let result = [];
        let score = Math.floor(this.score*2)/2;
        let hasDecimal = score%1 !==0;         //得出是否含有小数部分，true或false
        let integer = Math.floor(score);
        for(let i=0;i<integer;i++){          //得出整数部分的，'on'的个数
            result.push(cls_on);
        }
        if(hasDecimal){                       //得出小数部分，'half'的个数
            result.push(cls_half);
        }
        while (result.length<lengh){         //得出无星星的部分，'off'的个数
            result.push(cls_off);
        }
        return result;
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .star{font-size: 0;}
  .star .star-item{display: inline-block;}
  .star .star-item:last-child{margin-right: 0;}
  .star-48 .on{background: url("./star48_on@2x.png") no-repeat;}
  .star-48 .half{background: url("./star48_half@2x.png") no-repeat;}
  .star-48 .off{background: url("./star48_off@2x.png") no-repeat;}
  .star-48 .star-item{width: 20px;height: 20px;margin-right: 22px;background-size: cover;}

  .star-36 .on{background: url("./star36_on@2x.png") no-repeat;}
  .star-36 .half{background: url("./star36_half@2x.png") no-repeat;}
  .star-36 .off{background: url("./star36_off@2x.png") no-repeat;}
  .star-36 .star-item{width: 15px;height: 15px;margin-right: 6px;background-size: cover;}

  .star-24 .on{background: url("./star24_on@2x.png") no-repeat;}
  .star-24 .half{background: url("./star24_half@2x.png") no-repeat;}
  .star-24 .off{background: url("./star24_off@2x.png") no-repeat;}
  .star-24 .star-item{width: 10px;height: 10px;margin-right: 3px;background-size: cover;}

</style>



