<template>
  <div>
    <div class="shopcart">
      <div class="content" @click="togglelist">
        <div class="left">
          <div class="cart">
            <div class="logo" :class="{active:allcount>0}">
              <span class="icon-shopping_cart" :class="{active:allcount>0}"></span>
            </div>
            <div class="count" v-show="allcount>0">{{allcount}}</div>
          </div>
          <div class="price" :class="{active:allprice>0}">￥{{allprice}}</div>
          <div class="desc">另需配送费￥{{deliveryPrice}}元</div>
        </div>
        <div class="right" @click.stop.prevent="pay">
          <div class="pay" :class="payclass">{{paytext}}</div>
        </div>
      </div>
      <div class="ballwraper">
        <div v-for="ball in balls">
          <transition  name="drop" @before-enter="beforedrop" @enter="dropping" @after-enter="afterdrop">
            <div class="ballclass" v-show="ball.show">
              <div class="inner"></div>
            </div>
          </transition>
        </div>
      </div>
      <transition name="slideshow">
        <div class="shoplist" v-show="listshow">
          <div class="listhead">
            <h3 class="title">购物车</h3>
            <span class="empty" @click="clear">清空</span>
          </div>
          <div class="list-content" ref="listscroll">
            <ul>
              <li class="listfood" v-for="food in shopfood">
                <span class="name">{{food.name}}</span>
                <span class="price">￥{{food.price*food.count}}</span>
                <div class="cartcrl-box">
                  <cartcrl :food="food" @addevent="balldrop"></cartcrl>
                </div>
              </li>
            </ul>
          </div>
        </div>
      </transition>
    </div>
    <transition name="mask">
      <div class="listmask" v-show="listshow" @click="slide=true"></div>
    </transition>
  </div>
</template>

<script>
import cartcrl from '../cartcrl/cartcrl.vue';
import BScroll from 'better-scroll';

export default {

  data () {
    return {
      balls:[
        {
          show:false
         },
        {
          show:false
        },
        {
          show:false
        },
        {
          show:false
        },
        {
          show:false
        }
      ],
      dropballs:[],
      slide:true
    }
  },
  props:['deliveryPrice','minPrice','shopfood'],
  computed:{
    allprice:function () {         //计算总价
      let price = 0;
      for(let i=0;i<this.shopfood.length;i++){
          price+=this.shopfood[i].price*this.shopfood[i].count;
      }
      return price;
    },
    allcount:function () {           //计算总数
      let count = 0;
      for(let i=0;i<this.shopfood.length;i++){
        count+=this.shopfood[i].count;
      }
      return count;
    },
    paytext:function () {         //计算对应购买总价与起送的文字说明
      if(this.allprice ===0){
         return '￥'+this.minPrice+'起送';
      }else if(this.allprice < this.minPrice){
        let diff = this.minPrice - this.allprice;
        return '还差￥'+diff+'起送';
      }else{
          return '去结算';
      }
    },
    payclass:function () {            //计算不同起送条件下的样式
      if(this.allprice < this.minPrice){
        return 'noenough';
      }else{
        return 'enough';
      }
    },
    listshow:function () {      //购物车详情页的显示与隐藏
      if(!this.allcount){   //判断购物车选中的物品总数,没有,就说明没有购买,返回false
        this.slide = true;
        return false;
      }
      if(!this.slide){
          var that = this;
          this.$nextTick(function () {    //在购物清单中添加数量与减少数量的情况下,需要加载dom后再执行滚动条
            if(!that.listscroll){
              that.listscroll = new BScroll(that.$refs.listscroll,{
                click:true
              });
            }else {
                that.listscroll.refresh();      //重新刷新滚动条
            }
          })
      }
      return !this.slide;    //有购买的情况下,返回true
    }
  },
  methods:{
    balldrop:function (el) {    //作用在父组件中的函数调用
      for(let i=0;i<this.balls.length;i++){
          if(!this.balls[i].show){
              this.balls[i].show = true;    //找到balls数组中一个是其属性为false的数组元素,并设置其show属性为true
              this.balls[i].ele = el;   //保存由父组件传入的el元素对象
              this.dropballs.push(this.balls[i]);
              return;
          }
      }
    },
    beforedrop:function (el) {     //函数传参el为transition对象
      for(let i=this.balls.length-1;i>=0;i--){
        if(this.balls[i].show){
          let target = this.balls[i].ele.getBoundingClientRect();
          let x = target.left-32;
          let y = -(window.innerHeight-target.top-22);
          el.style.display = '';
          el.style.transform = 'translate3d(0,'+y+'px,0)';
          let inner = el.getElementsByClassName('inner')[0];
          inner.style.transform = 'translate3d('+x+'px,0,0)';
        }
      }
    },
    dropping:function (el,done) {
        let re = el.offsetHeight;   //重置动画
        this.$nextTick(function () {
          el.style.transform = 'translate3d(0,0,0)';
          let inner = el.getElementsByClassName('inner')[0];
          inner.style.transform = 'translate3d(0,0,0)';
          el.addEventListener('transitionend',done);
        });
    },
    afterdrop:function (el) {
        let ball = this.dropballs.shift();
        if(ball){
            ball.show = false;
            el.style.display='none';
        }

    },
    togglelist:function () {      //购物list的显示与升降关系
      if(!this.allcount){
          return;
      }
      this.slide = !this.slide;
    },
    clear:function () {     //清除购物车
      this.shopfood.forEach(function (food) {
          food.count=0;
      })
    },
    pay:function () {
      if(this.allprice >= this.minPrice){
        alert('请付款')
      }
    }
  },
  components:{
    cartcrl
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .shopcart{position: fixed;left: 0;bottom: 0;width: 100%;height: 48px;z-index: 50;}
  .shopcart .content{display: flex;background: #141d27;height: 100%;}
  .shopcart .content .left{flex-grow: 1;display: flex;}
  .shopcart .content .left .cart{position: relative;top:-10px;margin: 0 12px;padding: 6px;height: 56px;width: 56px;box-sizing: border-box;border-radius: 50%;background: #141d27;}
  .shopcart .content .left .cart .logo{width: 100%;height: 100%;text-align: center;background: #2b343c;border-radius: 50%;}
  .shopcart .content .left .cart .active{background: rgb(0,160,220);}
  .shopcart .content .left .cart .logo .icon-shopping_cart{font-size:24px;line-height: 44px;color: #80858a;}
  .shopcart .content .left .cart .logo .active{color: white;}
  .shopcart .content .left .cart .count{position: absolute;top: 0;right: 0;width: 24px;height: 16px;line-height: 16px;border-radius: 16px;font-weight: 700;color: white;background: red;text-align: center;font-size: 9px;box-shadow: 0 4px 8px rgba(0,0,0,0.4);}
  .shopcart .content .left .price{line-height: 24px;font-weight: 700;color: rgba(255,255,255,0.4);font-size: 16px;margin: 12px 0;padding-right: 12px;border-right:1px solid rgba(255,255,255,0.1);}
  .shopcart .content .left .active{color: white;}
  .shopcart .content .left .desc{line-height: 24px;font-weight: 200;color: rgba(255,255,255,0.4);font-size: 12px;margin: 12px 0;padding:0 12px;}
  .shopcart .content .right{min-width: 105px;text-align: center;}
  .shopcart .content .right .pay{font-size: 12px;color: rgba(255,255,255,0.4);line-height: 48px;font-weight: 700;}
  .shopcart .content .right .noenough{background: #2b343c;}
  .shopcart .content .right .enough{background: #00b43c;color: white;}

  .shopcart .ballclass{position: fixed;left: 32px;bottom: 22px;z-index: 200;transition:all 0.5s cubic-bezier(0.49, -0.29, 0.75, 0.41);}
  .shopcart .ballclass .inner{width: 16px;height: 16px;border-radius: 50%;background:rgb(0,160,220);transition: all 0.5s linear;}

  .shopcart .shoplist{position: absolute;left: 0;top:0;z-index: -1;width: 100%;transform: translateY(-100%);}
  .shopcart .shoplist.slideshow-enter-active,.shopcart .shoplist.slideshow-leave-active{transition: all 0.4s;}
  .shopcart .shoplist.slideshow-enter,.shopcart .shoplist.slideshow-leave-to{transform: translateY(0);}

  .shopcart .shoplist .listhead{height: 40px;line-height: 40px;padding: 0 18px;background: #f3f4f7;border-bottom: 1px solid rgba(7,17,27,0.1);}
  .shopcart .shoplist .listhead .title{float: left;font-size:14px;font-weight: 200;color: rgb(7,17,27);}
  .shopcart .shoplist .listhead .empty{float: right;font-size:12px;color: rgb(0,160,220);}
  .shopcart .shoplist .list-content{padding: 0 18px;max-height: 217px;background: white;overflow: hidden;}
  .shopcart .shoplist .list-content .listfood{position: relative;padding: 12px 0;box-sizing: border-box;}
  .shopcart .shoplist .list-content .listfood::after{position: absolute;content: '';left: 0;bottom: 0;width: 100%;border-bottom: 1px solid rgba(7,17,27,0.1);}
  .shopcart .shoplist .list-content .listfood .name{font-size: 14px;color: rgb(7,17,27);line-height: 24px;}
  .shopcart .shoplist .list-content .listfood .price{font-size: 14px;color: rgb(240,20,20);line-height: 24px;margin-right: 6px;margin-left: 18px;font-weight: 700;position: absolute;right: 90px;top:12px;}
  .shopcart .shoplist .list-content .listfood .cartcrl-box{position: absolute;right: 0;top:6px;}

  .listmask{position: fixed;left: 0;top:0;background: rgba(7,17,27,0.8);filter: blur(20px);width: 100%;height: 100%;z-index: 40;}
  .listmask.mask-enter-active,.listmask.mask-leave-active{transition: all 0.4s;}
  .listmask.mask-enter,.listmask.mask-leave-to{opacity: 0;}

</style>



