<template>
  <div id="app">
    <v-header :seller="seller"></v-header>
    <div class="tab">
      <div class="tab-item">
        <router-link to="goods">商品</router-link>
      </div>
      <div class="tab-item">
        <router-link to="ratings">评论</router-link>
      </div>
      <div class="tab-item">
        <router-link to="seller">商家</router-link>
      </div>
    </div>
    <keep-alive>
      <router-view :seller="seller"></router-view>
    </keep-alive>
  </div>
</template>

<script>
 import header from './components/header/header.vue';
 import {urlformat} from './common/js/urlformat.js';

 export default {
   data () {
     return {
       seller: {    //定义带商家id，即url地址中id参数的对象
         id:(function () {
            let result = urlformat();
            return result.id;
         })()
       }
     }
   },
   beforeCreate: function () {
     this.$router.replace('/goods');     //钩子中切换至指定路由
   },
   created: function () {
     var that = this;
     this.axios.get('/api/seller/?id'+this.seller.id).then(function (res) {
       if (res.data.errno === 0) {
         that.seller = Object.assign({},that.seller,res.data.data);  //assign方法，合并对象使用，参数(目标对象，源对象1，源对象2)
       }
     })
   },
   components: {
     'v-header': header
   }
 }
</script>

<style>
  .clearfix::after{content: '.';display: block;height: 0;clear: both;visibility: hidden;}
  #app .tab{display: flex;width: 100%;height: 40px;line-height: 40px;text-align: center;position: relative;}
  #app .tab::after{content: '';position: absolute;left: 0;bottom: 0;border-bottom: 1px solid rgba(7,17,27,0.1);width: 100%;}
  #app .tab .tab-item{flex-grow: 1;}
  #app .tab .tab-item a{font-size: 20px;color: rgb(77,85,93);display: block;}
  #app .tab .tab-item a.active{color: rgb(240,20,20);}
</style>



