<template>
  <div class="cartcrl">
    <transition name="move">
      <div class="decrease icon-remove_circle_outline" v-show="food.count>0" @click.stop.prevent="decreasecount"></div>
    </transition>
    <div class="count" v-show="food.count>0">{{food.count}}</div>
    <div class="add icon-add_circle" @click.stop.prevent="addcount($event)"></div>
  </div>
</template>

<script>
export default {

  data () {
    return {

    }
  },
  methods:{
    addcount:function (event) {   //父组件传入的food数组的元素对象中，没有count属性，由此方法，进行添加
      if(!this.food.count){
          this.$set(this.food,'count',1);
      }else {
          this.food.count++;
      }
      this.$emit('addevent',event.target);
    },
    decreasecount:function () {
      this.food.count--;
    }
  },
  props:['food']        //在父组的循环中，传入了每次循环的food，即json中foods
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.cartcrl .decrease,.cartcrl .add{display:inline-block;padding: 6px;font-size: 24px;line-height: 24px;color: rgb(0,160,220);}
.cartcrl .count{display: inline-block;line-height: 24px;color: rgb(147,153,159);font-size: 10px;width: 12px;padding-top: 6px;text-align: center;vertical-align: top;}

.move-enter-active,.move-leave-active{transition: all 0.5s;}
.move-enter,.move-leave-to{transform: translateX(24px) rotate(180deg);opacity: 0;}
</style>



