<template>
  <transition name="move">
    <div class="food" v-show="foodshow" ref="foodscroll">
      <div>
        <div class="img-box">
          <img :src="food.image"/>
          <div class="back">
            <i class="icon-arrow_lift" @click="foodshow=false"></i>
          </div>
        </div>
        <div class="content">
          <h3 class="name">{{food.name}}</h3>
          <div class="extra">
            <span class="month">月售{{food.sellCount}}份</span>
            <span>好评率{{food.rating}}%</span>
          </div>
          <div class="price">
            <span class="now">￥{{food.price}}</span>
            <span v-show="food.oldPrice" class="old">￥{{food.oldPrice}}</span>
          </div>
          <div class="cartcrl-box">
            <cartcrl :food="food" @addevent="addfood"></cartcrl>
          </div>
          <transition name="fade">
            <div class="buy" v-show="!food.count" @click.stop.prevent="addfirst($event)">加入购物车</div>
          </transition>
        </div>
        <split v-show="food.info"></split>
        <div class="info" v-show="food.info">
          <h3>商品介绍</h3>
          <p>{{food.info}}</p>
        </div>
        <split></split>
        <div class="rating">
          <h3>商品评价</h3>
          <ratingselect :ratings="foodratings" :selecttype="selecttype" :desc="desc" :onlycontent="onlycontent" @contenttype="contentchange" @ratingtype="typechange"></ratingselect>
          <div class="rating-box">
            <ul v-show="food.ratings">
              <li v-for="item in food.ratings" class="ratingitem" v-show="needshow(item.rateType,item.text)">
                <p class="time">{{item.rateTime | format}}</p>
                <p class="text">
                  <span :class="[item.rateType?'icon-thumb_down':'icon-thumb_up']"></span>{{item.text}}
                </p>
                <div class="user">
                  <span>{{item.username}}</span>
                  <img :src="item.avatar" width="12" height="12"/>
                </div>
              </li>
            </ul>
            <p v-show="!food.ratings || !food.ratings.length" class="norating">暂时没有评论</p>
          </div>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
import BScroll from 'better-scroll';
import cartcrl from '../cartcrl/cartcrl.vue';
import split from '../split/split.vue';
import ratingselect from '../ratingselect/ratingselect.vue';
import {formatdate} from '../../common/js/date.js';

const all = 2;
const verygood = 0;
const normal = 1;

export default {

  data () {
    return {
      foodshow:false,
      selecttype: all,
      onlycontent : false,
      desc:{
        all:'全部',
        verygood:'推荐',
        normal:'吐槽'
      },
      foodratings:[]
    }
  },
  props:['food'],
  filters:{
    format:function (time) {
       var date = new Date(time);    //将时间戳time传入生成当时的date对象
       return formatdate(date,'yyyy-MM-dd hh:mm');    //执行对应格式条件的函数
    }
  },
  methods:{
    show:function () {
      this.foodshow = true;  //初始化food组件的传入值
      this.selecttype = all;
      this.onlycontent = false;
      var that = this;
      this.$nextTick(function () {
          if(!that.foodscroll){        //判断有没有scroll实例，没有就新建，有就refresh方法刷新
             that.foodscroll = new BScroll(that.$refs.foodscroll,{
                 click:true
             })
          }else {
              that.foodscroll.refresh();
          }
          that.foodratings = that.food.ratings;   //food给予子组件ratingselect传值延迟
      })
    },
    addfood:function (el) {        //先从子组件cartcrl接收自定义事件的传值el，再自定义事件传到父组件goods上，通过父组件接收，执行shopcart的小球动画
      this.$emit('addevent',el);
    },
    addfirst:function (event) {
      this.$set(this.food,'count',1);
      this.$emit('addevent',event.target);  //自定义事件传到父组件goods上，通过父组件接收，执行shopcart的小球动画
    },
    typechange:function (type) {
      this.selecttype = type;
      var that = this;
      this.$nextTick(function () {
        that.foodscroll.refresh();
      })
    },
    contentchange:function (content) {
      this.onlycontent = content;
      var that = this;
      this.$nextTick(function () {
        that.foodscroll.refresh();
      })
    },
    needshow:function (type,text) {   //判断每一个rating元素
      if(this.onlycontent && !text){
         return false;
      }
      if(this.selecttype === all){
         return true;
      }else{
          return type === this.selecttype;
      }
    }
  },
  components:{
    cartcrl,split,ratingselect
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .food{position: fixed;left: 0;top:0;bottom: 48px;background: white;z-index: 30;width: 100%;}
  .food.move-enter-active,.food.move-leave-active{transition: all 0.5s;}
  .food.move-enter,.food.move-leave-to{transform: translateX(100%);}
  .food .img-box{width: 100%;height: 0;padding-top: 100%;position: relative;}
  .food .img-box img{height: 100%;width: 100%;position: absolute;left: 0;top:0;}
  .food .img-box .back{position: absolute;top:10px;left: 10px;}
  .food .img-box .back .icon-arrow_lift{color: white;font-size: 18px;display: block;padding: 6px;}
  .food .content{padding: 18px;position: relative;}
  .food .content .name{font-size: 14px;font-weight: 700;color: rgb(7,17,27);line-height: 14px;margin-bottom: 8px;height: 14px;}
  .food .content .extra{margin-bottom: 18px;font-size: 10px;color: rgb(147,153,159);line-height: 10px;height: 10px;}
  .food .content .extra .month{margin-right: 12px;}
  .food .content .price{line-height: 24px;height: 24px;font-weight: 700;font-size: 0;}
  .food .content .price .now{margin-right: 8px;color: rgb(240,20,20);font-size: 14px;font-weight: 700;}
  .food .content .price .old{color: rgb(147,153,159);font-size: 10px;text-decoration: line-through;}
  .food .content .cartcrl-box{position: absolute;bottom: 12px;right: 12px;}
  .food .content .buy{position: absolute;bottom: 18px;right: 18px;padding: 0 12px;font-size: 10px;line-height: 24px;height: 24px;color: white;background: rgb(0,160,220);border-radius: 12px;text-align: center;z-index: 10;}
  .food .content .buy.fade-enter-active,.food .content .buy.fade-leave-active{transition: all 0.2s;}
  .food .content .buy.fade-enter,.food .content .buy.fade-leave-to{opacity: 0;}

  .food .info{padding: 18px;}
  .food .info h3{margin-bottom: 6px;line-height: 14px;font-size: 14px;color: rgb(7,17,27);}
  .food .info p{padding:0 8px;line-height: 24px;font-size: 12px;color: rgb(77,85,93);font-weight: 200;}
  .food .rating{padding-top: 18px;}
  .food .rating h3{line-height: 14px;font-size: 14px;color: rgb(7,17,27);padding:0 18px;}
  .food .rating .rating-box{padding: 0 18px;}
  .food .rating .rating-box .ratingitem{padding: 16px 0;position: relative;border-bottom: 1px solid rgba(7,17,27,0.1);}
  .food .rating .rating-box .ratingitem .time{font-size: 10px;color: rgb(147,153,159);line-height: 12px;margin-bottom: 6px;}
  .food .rating .rating-box .ratingitem .text{font-size: 12px;color: rgb(7,17,27);line-height: 16px;}
  .food .rating .rating-box .ratingitem .text .icon-thumb_up{font-size: 12px;line-height: 24px;margin-right: 4px;color: rgb(0,160,220);}
  .food .rating .rating-box .ratingitem .text .icon-thumb_down{font-size: 12px;line-height: 24px;margin-right: 4px;color: rgb(147,153,159);}
  .food .rating .rating-box .ratingitem .user{position: absolute;right: 0;top: 16px;}
  .food .rating .rating-box .ratingitem .user span{font-size: 10px;color: rgb(147,153,159);line-height: 12px;margin-right: 6px;display: inline-block;vertical-align: top;}
  .food .rating .rating-box .ratingitem .user img{border-radius: 50%;vertical-align: top;}
  .food .rating .rating-box .norating{font-size: 12px;line-height: 16px;color:black;}
</style>



