<template>
  <div class="ratings" ref="ratingsscroll">
    <div class="ratings-box">
      <div class="top">
        <div class="left">
          <h1 class="score">{{seller.score}}</h1>
          <p class="title">综合评分</p>
          <p class="rank">高于周边商家{{seller.rankRate}}%</p>
        </div>
        <div class="right">
          <div class="score-box">
            <span class="title">服务评分</span>
            <star :size="36" :score="seller.serviceScore"></star>
            <span class="score">{{seller.serviceScore}}</span>
          </div>
          <div class="score-box">
            <span class="title">商品评分</span>
            <star :size="36" :score="seller.foodScore"></star>
            <span class="score">{{seller.foodScore}}</span>
          </div>
          <div class="delivery">
            <span class="title">送达时间</span>
            <span class="time">{{seller.deliveryTime}}分钟</span>
          </div>
        </div>
      </div>
      <split></split>
      <ratingselect :ratings="ratings" :selecttype="selecttype" :desc="desc" :onlycontent="onlycontent" @contenttype="contentchange" @ratingtype="typechange"></ratingselect>
      <div class="ratings-content">
        <ul>
          <li v-for="rating in ratings" class="ratingitem" v-show="needshow(rating.rateType,rating.text)">
            <div class="avatar">
              <img :src="rating.avatar" width="28" height="28"/>
            </div>
            <div class="content">
              <span class="username">{{rating.username}}</span>
              <div class="score">
                <star :size="24" :score="rating.score"></star>
                <span class="time" v-show="rating.deliveryTime">{{rating.deliveryTime}}分钟送达</span>
              </div>
              <p class="text" v-show="rating.text">{{rating.text}}</p>
              <div class="recommend" v-show="rating.recommend && rating.recommend.length">
                <span class="icon-thumb_up"></span>
                <span v-for="item in rating.recommend" class="recommend-item">{{item}}</span>
              </div>
              <p class="rateTime">{{rating.rateTime | format}}</p>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import star from '../star/star.vue';
import split from '../split/split.vue';
import ratingselect from '../ratingselect/ratingselect.vue';
import BScroll from 'better-scroll';
import {formatdate} from '../../common/js/date.js';

const all = 2;
const verygood = 0;
const normal = 1;

export default {

  data () {
    return {
      selecttype: all,
      onlycontent : false,
      desc:{
        all:'全部',
        verygood:'满意',
        normal:'不满意'
      },
      ratings:[]
    }
  },
  created:function () {
    var that = this;
    this.axios.get('/api/ratings').then(function (res) {
      if (res.data.errno === 0) {
        that.ratings = res.data.data;
        that.initscroll();
      }
    })
  },
  props:['seller'],
  methods:{
    initscroll:function () {
      this.ratingsscroll = new BScroll(this.$refs.ratingsscroll,{
          click:true
      })
    },
    typechange:function (type) {
      this.selecttype = type;
      var that = this;
      this.$nextTick(function () {
        that.ratingsscroll.refresh();
      })
    },
    contentchange:function (content) {
      this.onlycontent = content;
      var that = this;
      this.$nextTick(function () {
        that.ratingsscroll.refresh();
      })
    },
    needshow:function (type,text) {   //判断每一个rating元素
      if(this.onlycontent && !text){
        return false;
      }
      if(this.selecttype === all){
        return true;
      }else{
        return type === this.selecttype;
      }
    }
  },
  filters:{
    format:function (time) {
      var date = new Date(time);    //将时间戳time传入生成当时的date对象
      return formatdate(date,'yyyy-MM-dd hh:mm');    //执行对应格式条件的函数
    }
  },
  components:{
      star,split,ratingselect
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .ratings{position: absolute;top:174px;bottom: 0;left: 0;width: 100%;overflow: hidden;}
  .ratings .top{display: flex;padding: 18px 0;}
  .ratings .top .left{flex: 0 0 137px;width: 137px;text-align: center;padding: 6px 0;border-right:1px solid rgba(7,17,27,0.1);}
  @media screen and (max-width: 320px){
    .ratings .top .left{flex: 0 0 120px;width: 120px;}
  }
  .ratings .top .left .score{font-size: 24px;line-height: 28px;color: rgb(255,153,0);margin-bottom: 6px;}
  .ratings .top .left .title{font-size: 12px;line-height: 12px;color: rgb(7,17,27);margin-bottom: 8px;}
  .ratings .top .left .rank{font-size: 10px;line-height: 10px;color: rgb(147,153,159);}
  .ratings .top .right{flex-grow: 1;padding: 6px 0 6px 24px;}
  @media screen and (max-width: 320px){
    .ratings .top .right{padding: 6px 0 6px 6px;}
  }
  .ratings .top .right .score-box{margin-bottom: 8px;font-size: 0;}
  .ratings .top .right .score-box .title{font-size: 12px;line-height: 18px;color: rgb(7,17,27);display: inline-block;vertical-align: top;}
  .ratings .top .right .score-box .star{display: inline-block;margin: 0 12px;vertical-align: top;}
  .ratings .top .right .score-box .score{font-size: 12px;line-height: 18px;color: rgb(255,153,0);display: inline-block;vertical-align: top;}
  .ratings .top .right .delivery{font-size: 0;}
  .ratings .top .right .delivery .title{font-size: 12px;line-height: 18px;color: rgb(7,17,27);}
  .ratings .top .right .delivery .time{font-size: 12px;line-height: 18px;color: rgb(147,153,159);margin-left: 12px;}

  .ratings .ratings-content{padding: 0 18px;}
  .ratings .ratings-content .ratingitem{display: flex;padding: 18px 0;border-bottom: 1px solid rgba(7,17,27,0.1);}
  .ratings .ratings-content .ratingitem .avatar{width: 28px;flex-grow: 0;margin-right: 12px;}
  .ratings .ratings-content .ratingitem .avatar img{border-radius: 50%;}
  .ratings .ratings-content .ratingitem .content{flex-grow: 1;position: relative;}
  .ratings .ratings-content .ratingitem .content .username{font-size: 10px;line-height: 12px;color: rgb(7,17,27);margin-bottom: 4px;display: inline-block;}
  .ratings .ratings-content .ratingitem .content .score{font-size: 0;}
  .ratings .ratings-content .ratingitem .content .score .star{display: inline-block;}
  .ratings .ratings-content .ratingitem .content .score .time{font-size: 10px;line-height: 12px;font-weight: 200;color: rgb(147,153,159);margin-left: 6px;}
  .ratings .ratings-content .ratingitem .content .text{font-size: 12px;line-height: 18px;color: rgb(7,17,27);padding-top: 6px;}
  .ratings .ratings-content .ratingitem .content .recommend{font-size: 0;padding-top: 8px;}
  .ratings .ratings-content .ratingitem .content .recommend .icon-thumb_up{font-size: 12px;line-height: 16px;color: rgb(0,160,220);margin-right: 8px;display: inline-block;vertical-align: top;margin-bottom: 4px;}
  .ratings .ratings-content .ratingitem .content .recommend .recommend-item{font-size: 9px;line-height: 16px;color: rgb(147,153,159);margin-right: 8px;padding: 0 6px;border-radius: 1px;border: 1px solid rgba(7,17,27,0.1);display: inline-block;overflow: hidden;text-overflow: ellipsis;white-space: nowrap;width: 50px;vertical-align: top;margin-bottom: 4px;text-align: center;background: white;}
  .ratings .ratings-content .ratingitem .content .rateTime{position: absolute;right: 0;top: 0;font-size: 10px;line-height: 12px;font-weight: 200;color: rgb(147,153,159);}
</style>



