<template>
  <div class="ratingselect">
    <div class="ratingtype">
      <span class="one" :class="{active:selecttype===2}" @click="select(2)">{{desc.all}}<i>{{ratings.length}}</i></span>
      <span class="one" :class="{active:selecttype===0}" @click="select(0)">{{desc.verygood}}<i>{{verygoods.length}}</i></span>
      <span class="two" :class="{active:selecttype===1}" @click="select(1)">{{desc.normal}}<i>{{normals.length}}</i></span>
    </div>
    <div class="switch" @click="togglecontent">
      <span class="icon-check_circle" :class="{active:contenttype}"></span>
      <span class="text">只看有内容的评价</span>
    </div>
  </div>
</template>

<script>
const all = 2;
const verygood = 0;
const normal = 1;
export default {

  data () {
    return {
      contenttype : this.onlycontent,
    }
  },
  props:['ratings','selecttype','desc','onlycontent'],
  methods:{
    select:function (type) {
       this.$emit('ratingtype',type);
    },
    togglecontent:function () {
       this.contenttype = !this.contenttype;
       this.$emit('contenttype',this.contenttype);
    }
  },
  computed:{
    verygoods:function () {
      return this.ratings.filter(function (item) {
        return item.rateType === verygood;
      })
    },
    normals:function () {
      return this.ratings.filter(function (item) {
        return item.rateType === normal;
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .ratingselect .ratingtype{padding: 18px 0;margin: 0 18px;border-bottom: 1px solid rgba(7,17,27,0.1);font-size: 0;}
  .ratingselect .ratingtype span{padding: 8px 12px;text-align: center;margin-right: 8px;display: inline-block;border-radius: 1px;font-size: 12px;line-height: 16px;}
  .ratingselect .ratingtype .one{background:rgba(0,160,220,0.2);color: rgb(77,85,93);}
  .ratingselect .ratingtype .two{background:rgba(77,85,92,0.2);color: rgb(77,85,93);}
  .ratingselect .ratingtype span i{font-style: normal;font-size: 8px;margin-left: 2px;}
  .ratingselect .ratingtype .one.active{background:rgb(0,160,220);color: white;}
  .ratingselect .ratingtype .two.active{background:rgb(77,85,92);color: white;}

  .ratingselect .switch{padding: 12px 16px;border-bottom: 1px solid rgba(7,17,27,0.1);line-height: 24px;color: rgb(147,153,159);font-size:0; }
  .ratingselect .switch .icon-check_circle{display: inline-block;font-size: 24px;margin-right: 4px;vertical-align: top;}
  .ratingselect .switch .active{color: #00c850;}
  .ratingselect .switch .text{display: inline-block;font-size: 12px;vertical-align: top;}
</style>



