<template>
  <div class="seller" ref="sellerscroll">
    <div class="seller-content">
      <div class="top">
        <h1 class="title">{{seller.name}}</h1>
        <div class="desc">
          <star :size="36" :score="seller.score"></star>
          <span class="text">({{seller.ratingCount}})</span>
          <span class="text">月售{{seller.sellCount}}单</span>
        </div>
        <ul>
          <li class="box">
            <h3>起送价</h3>
            <div class="content"><span class="number">{{seller.minPrice}}</span>元</div>
          </li>
          <li class="box">
            <h3>商家配送</h3>
            <div class="content"><span class="number">{{seller.deliveryPrice}}</span>元</div>
          </li>
          <li class="box">
            <h3>平均配送时间</h3>
            <div class="content"><span class="number">{{seller.deliveryTime}}</span>元</div>
          </li>
        </ul>
        <div class="fav" @click.stop.prevent="togglefav">
          <span class="icon-favorite" :class="{'active':favorite}"></span>
          <h3>{{favtext}}</h3>
        </div>
      </div>
      <split></split>
      <div class="bulletin">
        <h1 class="title">公告与活动</h1>
        <div class="text-box">
          <p class="text">{{seller.bulletin}}</p>
        </div>
        <ul v-if="seller.supports" class="support">
          <li v-for="val in seller.supports" class="item">
            <span class="icon" :class="classmap[val.type]"></span>
            <span class="text">{{val.description}}</span>
          </li>
        </ul>
      </div>
      <split></split>
      <div class="pictures">
        <h1 class="title">商家实景</h1>
        <div class="pic-wrapper" ref="picscroll">
          <ul ref="picul">
            <li v-for="(pic,index) in seller.pics" class="picbox">
              <img :src="pic" height="90" width="120"/>
            </li>
          </ul>
        </div>
      </div>
      <split></split>
      <div class="info">
        <h1 class="title">商家信息</h1>
        <ul>
          <li class="infoitem" v-for="text in seller.infos">{{text}}</li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import star from '../star/star.vue';
import split from '../split/split.vue';
import BScroll from 'better-scroll';
import {savestore,readstore} from '../../common/js/localstore.js';
export default {

  data () {
    return {
      classmap:['decrease','discount','guarantee','invoice','special'],
      favorite:(() => {
         return readstore(this.seller.id,'fav',false);     //立即执行函数中,this的指向变化，需要使用箭头函数替代
      })()
    }
  },
  props:['seller'],
  mounted:function () {
    var that =this;
    this.$nextTick(function () {
      that.sellerscrollinit();
      that.picscrollinit();
    })
  },
  watch:{
    seller:function () {    //依赖seller的变化,执行回调函数,刚生成页面时,seller没有对象
      var that = this;
      this.$nextTick(function () {
        that.sellerscrollinit();
        that.picscrollinit();
      });
    }
  },
  computed:{
    favtext:function () {       //计算收藏的文字，根据favorite的值来确定
      return this.favorite?'已收藏':'未收藏';
    }
  },
  methods:{
    togglefav:function () {
        this.favorite =!this.favorite;
        savestore(this.seller.id,'fav',this.favorite);
    },
    sellerscrollinit:function () {
      if(!this.sellerscroll){
        this.sellerscroll = new BScroll(this.$refs.sellerscroll,{
          click:true
        })
      }else{
        this.sellerscroll.refresh();
      }
    },
    picscrollinit:function () {
      let picwidth = 120;
      let margin = 6;
      let width = (picwidth+margin)*this.seller.pics.length-margin;  //计算ul的宽度,better-scroll的原理,子元素的大小超过父元素,才生效better-scroll滚动
      this.$refs.picul.style.width = width+'px';
      if(!this.picscroll){
        this.picscroll = new BScroll(this.$refs.picscroll,{
          click:true,
          scrollX:true,
          eventPassthrough:'vertical'
        })
      }else{
        this.picscroll.refresh();
      }
    }
  },
  components:{
      star,split
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .seller{position: absolute;top:174px;bottom: 0;overflow: hidden;width: 100%;}
  .seller .top{padding: 18px;position: relative;}
  .seller .top .title{font-size: 14px;line-height: 14px;color: rgb(7,17,27);margin-bottom: 8px;font-weight: 200;}
  .seller .top .desc{padding-bottom: 18px;border-bottom: 1px solid rgba(7,17,27,0.1);font-size: 0;margin-bottom: 18px;}
  .seller .top .desc .star{margin-right: 8px;display: inline-block;vertical-align: top;}
  .seller .top .desc .text{margin-right: 12px;font-size: 10px;line-height: 18px;color: rgb(77,85,93);}
  .seller .top .fav{position: absolute;right: 18px;top:18px;text-align: center;}
  .seller .top .fav span{margin-bottom: 4px;display: inline-block;font-size: 24px;line-height: 24px;color: #d4d6d9;}
  .seller .top .fav .active{color: rgb(240,20,20);}
  .seller .top .fav h3{font-size: 10px;line-height: 10px;color: rgb(77,85,93);}
  .seller .top ul{display: flex;}
  .seller .top .box{text-align: center;flex-grow: 1;flex-basis: 0;}
  .seller .top .box:nth-child(2){border-left: 1px solid rgba(7,17,27,0.1);border-right: 1px solid rgba(7,17,27,0.1);}
  .seller .top .box h3{font-size: 10px;line-height: 10px;color: rgb(147,153,159);margin-bottom: 4px;}
  .seller .top .box .content{font-size: 10px;line-height: 24px;color: rgb(7,17,27);font-weight: 200;}
  .seller .top .box .content .number{font-size: 24px;line-height: 24px;color: rgb(7,17,27);font-weight: 200;}

  .seller .bulletin{padding: 18px 18px 0;}
  .seller .bulletin .title{margin-bottom: 8px;font-size: 14px;line-height: 14px;color: rgb(7,17,27);}
  .seller .bulletin .text-box{padding: 0 12px 16px;border-bottom: 1px solid rgba(7,17,27,0.1);}
  .seller .bulletin .text-box .text{font-size: 12px;line-height: 24px;font-weight: 200;color: rgb(240,20,20);}
  .seller .bulletin .support .item{padding:16px 12px;border-bottom: 1px solid rgba(7,17,27,0.1);font-size: 0;}
  .seller .bulletin .support .item:last-child{border-bottom: none;}
  .seller .bulletin .support .decrease{background: url("./decrease_4@2x.png") no-repeat;}
  .seller .bulletin .support .discount{background: url("./discount_4@2x.png") no-repeat;}
  .seller .bulletin .support .guarantee{background: url("./guarantee_4@2x.png") no-repeat;}
  .seller .bulletin .support .invoice{background: url("./invoice_4@2x.png") no-repeat;}
  .seller .bulletin .support .special{background: url("./special_4@2x.png") no-repeat;}
  .seller .bulletin .support .icon{display: inline-block;height: 16px;width: 16px;background-size: cover;vertical-align: top;margin-right: 6px;}
  .seller .bulletin .support .item .text{font-size: 12px;line-height: 16px;font-weight: 200;color: rgb(7,17,27);display: inline-block;vertical-align: top;}

  .seller .pictures{padding: 18px 0 18px 18px;}
  .seller .pictures .title{margin-bottom: 12px;font-size: 14px;line-height: 14px;color: rgb(7,17,27);}
  .seller .pictures .pic-wrapper{overflow: hidden;white-space: nowrap;}
  .seller .pictures .pic-wrapper .picbox{height: 90px;width: 120px;display: inline-block;margin-right: 6px;font-size: 0;}

  .seller .info{padding: 18px 18px 0 18px;}
  .seller .info .title{margin-bottom: 12px;font-size: 14px;line-height: 14px;color: rgb(7,17,27);}
  .seller .info .infoitem{padding: 16px 12px;border-top: 1px solid rgba(7,17,27,0.1);font-size: 12px;line-height: 16px;font-weight: 200;color: rgb(7,17,27);}
</style>



