<template>
  <div class="goods">
    <div class="menu" ref="menuscroll">
      <ul>
        <li v-for="(item,index) in foods" class="menu-item" :class="{active:curindex===index}" @click="selectmenu(index)">
          <span class="text">
             <span class="icon" :class="classmap[item.type]" v-show="item.type>0"></span>{{item.name}}
          </span>
        </li>
      </ul>
    </div>
    <div class="foods" ref="foodscroll">
      <ul>
        <li v-for="item in foods" class="item-list food-list-hook">
          <h1 class="title">{{item.name}}</h1>
          <ul>
            <li v-for="food in item.foods" class="food-class" @click.stop.prevent="selfood(food)">
              <div class="icon">
                <img :src="food.icon" width="57"/>
              </div>
              <div class="content">
                <h2 class="name">{{food.name}}</h2>
                <p class="description" v-show="food.description">{{food.description}}</p>
                <div class="extra">
                  <span class="month">月售{{food.sellCount}}份</span><span>好评率{{food.rating}}%</span>
                </div>
                <div class="price">
                  <span class="now">￥{{food.price}}</span><span v-show="food.oldPrice" class="old">￥{{food.oldPrice}}</span>
                </div>
                <div class="cartcrl-box">
                  <cartcrl :food="food" @addevent="ballel"></cartcrl>
                </div>
              </div>
            </li>
          </ul>
        </li>
      </ul>
    </div>
    <shopcart :shopfood="selectfood" :deliveryPrice="seller.deliveryPrice" :minPrice="seller.minPrice" ref="shopcart"></shopcart>
    <food :food="seledfood" ref="food" @addevent="ballel"></food>
  </div>
</template>

<script>

import BScroll from 'better-scroll';
import shopcart from '../shopcart/shopcart.vue';
import cartcrl from '../cartcrl/cartcrl.vue';
import food from '../food/food.vue';

export default {

  data () {
    return {
      classmap:['decrease','discount','guarantee','invoice','special'],
      foods:[],
      listheight:[],
      scrollY:0,
      seledfood:{}
    }
  },
  props:['seller'],
  created:function () {
    var that = this;
    this.axios.get('/api/goods').then(function (res) {
      if (res.data.errno === 0) {
        that.foods = res.data.data;
        that.$nextTick(function () {  //加载DOM后运行函数，类似setTimeout
            that.initscroll();
            that.calheight();
        })
      }
    })
  },
  computed:{
     curindex:function () { //值随着scrollY的事件变化而变化，得到实时food区滚动对应的li元素的index
       for(let i=0;i<this.listheight.length;i++){
           let height1 = this.listheight[i];
           let height2 = this.listheight[i+1];
           if(!height2 || this.scrollY >= height1 && this.scrollY < height2){
               return i;
           }
       }
       return 0;
     },
    selectfood:function () {        //变化的数据，统计有购买数量count的对象，加入数组
       let selfood=[];
       this.foods.forEach(function (item) {
          item.foods.forEach(function (food) {
             if(food.count>0){
               selfood.push(food);
             }
          })
       });
       return selfood;
    }
  },
  methods:{
    initscroll:function () {
      this.menuscroll = new BScroll(this.$refs.menuscroll,{   //对象方式创建滚动条实例
          click:true             //better-scroll开启click事件
      });
      this.foodscroll = new BScroll(this.$refs.foodscroll,{   //对象方式创建滚动条实例
          probeType:3,            //better-scroll开启click事件
          click:true
      });
      var that = this;
      this.foodscroll.on('scroll',function (pos) {   //监听滚动条事件，pos事件对象为滚动的坐标，better-scroll事件监听方法，得到滚动的实时y坐标
          that.scrollY = Math.abs(Math.round(pos.y));
      })
    },
    calheight:function () {               //计算food区每个大li元素的高度，加入数组
       let foodlist = this.$refs.foodscroll.getElementsByClassName('food-list-hook');
       let height = 0;
       this.listheight.push(height);
       for(let i=0;i<foodlist.length;i++){
         height+=foodlist[i].clientHeight;    //得到每个li元素至ul元素顶部的距离，li元素的累加
         this.listheight.push(height);
       }
    },
    selectmenu:function (index) {
      let foodlist = this.$refs.foodscroll.getElementsByClassName('food-list-hook');
      this.foodscroll.scrollToElement(foodlist[index],500);    //better-scroll方法，滚动到指定元素目标
    },
    ballel:function (el) {      //子组件的监听函数
      var that = this;
      this.$nextTick(function () {
        that.$refs.shopcart.balldrop(el);    //运行另一子组件的方法,使用组件ref,指向子组件实例,并运行子组件上的方法
      });
    },
    selfood:function (food) {     //点选指定的项,并且由seledfood传入子组件food
      this.seledfood = food;
      this.$refs.food.show();
    }
  },
  components:{
      shopcart,cartcrl,food
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .goods{display: flex;position: absolute;top:174px;bottom: 46px;overflow: hidden;width: 100%;}

  .goods .menu{flex-grow: 0;width: 80px;background: #f3f5f7;}
  .goods .menu .menu-item{width: 56px;line-height: 14px;height: 54px;display: flex;align-items: center;padding: 0 12px;position: relative;}
  .goods .menu .active{z-index: 10;margin-top: -1px;background: white;}
  .goods .menu .menu-item::after{content: '';position: absolute;left: 0;bottom: 0;border-bottom: 1px solid rgba(7,17,27,0.1);width: 100%;}
  .goods .menu .active::after{display: none;}
  .goods .menu .menu-item .text{font-size: 12px;color: rgb(77,85,93);font-weight: 200;}
  .goods .menu .active .text{font-weight: 700;}
  .goods .menu .menu-item .text .decrease{background: url("./decrease_3@2x.png") no-repeat;}
  .goods .menu .menu-item .text .discount{background: url("./discount_3@2x.png") no-repeat;}
  .goods .menu .menu-item .text .guarantee{background: url("./guarantee_3@2x.png") no-repeat;}
  .goods .menu .menu-item .text .invoice{background: url("./invoice_3@2x.png") no-repeat;}
  .goods .menu .menu-item .text .special{background: url("./special_3@2x.png") no-repeat;}
  .goods .menu .menu-item .text .icon{display: inline-block;height: 12px;width: 12px;background-size: cover;vertical-align: middle;margin-right: 2px;}

  .goods .foods{flex-grow: 1;}
  .goods .foods .item-list .title{height: 26px;line-height: 26px;font-size: 12px;color: rgb(147,153,159);background: #f3f5f7;border-left:2px solid #d9dde1;padding-left: 14px;}
  .goods .foods .item-list .food-class{display: flex;margin:0 18px;padding: 18px 0;position: relative;}
  .goods .foods .item-list .food-class::after{content: '';position: absolute;left: 0;bottom: 0;border-bottom: 1px solid rgba(7,17,27,0.1);width: 100%;}
  .goods .foods .item-list .food-class:last-child::after{display: none;}
  .goods .foods .item-list .food-class .icon{margin-right: 10px;}
  .goods .foods .item-list .food-class .content{flex-grow: 1;position: relative;}
  .goods .foods .item-list .food-class .content .name{margin: 2px 0 8px 0;font-size: 14px;color: rgb(7,17,27);line-height: 14px;height: 14px;}
  .goods .foods .item-list .food-class .content .description{margin-bottom: 8px;font-size: 10px;color: rgb(147,153,159);line-height: 10px;}
  .goods .foods .item-list .food-class .content .extra{margin-bottom: 8px;font-size: 10px;color: rgb(147,153,159);line-height: 10px;height: 10px;}
  .goods .foods .item-list .food-class .content .extra .month{margin-right: 12px;}
  .goods .foods .item-list .food-class .content .price{line-height: 24px;height: 24px;font-weight: 700;font-size: 0;}
  .goods .foods .item-list .food-class .content .price .now{margin-right: 8px;color: rgb(240,20,20);font-size: 14px;font-weight: 700;}
  .goods .foods .item-list .food-class .content .price .old{color: rgb(147,153,159);font-size: 10px;text-decoration: line-through;}
  .goods .foods .item-list .food-class .content .cartcrl-box{position: absolute;right: 0;bottom: -6px;}

</style>



