export function formatdate(date,regexp) {
  if(/(y+)/.test(regexp)){   //对应年的正则替换，RegExp.$1对应第一个子匹配的元素
    regexp = regexp.replace(RegExp.$1,(date.getFullYear()+'').substring(4-RegExp.$1.length));
  }       //+''为转化为字符串
  var ob={
    'M+':date.getMonth()+1,
    'd+':date.getDate(),
    'h+':date.getHours(),
    'm+':date.getMinutes(),
    's+':date.getSeconds()
  };
  for(var key in ob){
    if(new RegExp('('+key+')').test(regexp)){  //因为key是动态的，所以使用拼接的方式创建正则对象
      var str = ob[key]+'';
      regexp = regexp.replace(RegExp.$1,RegExp.$1.length===1?str:addstr(str));
    }   //拿'yyyy-MM-dd hh:mm'去匹配正则，RegExp.$1为'MM'，length为2，单数月份要加'0'
  }
  return regexp;
};

function addstr(str) {    //执行加0并，响应的字符串裁切
   return ('00'+str).substring(str.length);
}



